@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Outfit", sans-serif;
}

body{
  background: #000;
}

h1, h4, p,a{
  color: #fff;
  text-decoration: none;

}

ul{
  list-style-type: none;
}

.btn {
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: #D885A3;
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
}


.btn:hover {
  background: rgba(255,255,255, 0.2);
  color: #fff;
  transition: 0.3s;
}


.header{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem ;
    position: fixed;
    width:100%;
    height: 90px;
    z-index: 10;

}

.header-bg{
    background-color: rgba(0,0,0,0.9);
    transition: 0.5s;
}
.nav-menu{
    display:flex;
} 
.nav-menu li{
    padding: 0 1rem;
} 

.nav-menu li a{
    font-size: 1.2rem ;
    font-weight: 500;
} 

.hamburger{
    display: none;
}

@media screen and (max-width: 1040px) {
    .nav-menu{
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100vh;
      background: rgba(0,0,0,0.9);
      position: absolute;
      top: 0;
      left: -100%;
      z-index: -3;
      transition: 0.3s;
    }

    .nav-menu.active{
        left: 0;
    }
    .nav-menu li{
      padding: 1rem 0;
    }
  
    .nav-menu li a{
      font-size: 2rem;
    }

    .hamburger{
        display: initial;
    }
    
  }
.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.background{
    width:100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content:"";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.6;
}

.hero{
    height: 100%;
    width: 100%;
}
.hero .content{
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;

}

.hero .content h1 {
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}

.hero .content h2 {
    font-size: 1.4rem;
    font-weight: 200;
    color: white;
    text-transform:uppercase;
}


@media screen and (max-width: 640px) {
    .hero .content h1 {
        font-size: 3rem;

    }
    
    .hero .content h2 {
        font-size: 1.4rem;
        color: white;
    }  

}
.about{
    padding: 10rem;
    background: rgb(46, 45, 45);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

.about .left{

    text-align: center;
    margin:auto ;
    padding: 1rem;
    max-width: 600px;

}

.about .left h1{
    font-style: italic;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 50px;

}

.about .left p{
    margin: 1.2rem 0;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 20px;
}
.about.right {
    max-width: 700px;
}

.right .img-container{
    text-align: center;
    align-items: center;
    position: relative;
}

.right .img{
    max-width: 90%;
    border: 1px solid ;
}
.skills{
    width: 100%;
    padding: 6rem 1rem;
    background: rgb(46, 45, 45);
}
.card-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:40px;
    gap:40px;
}


.card{
    border: 1px solid #b98e7a;
    color: #b98e7a !important;
    text-align: center;
    padding: 1rem;
}
.card p{
    padding: 10px 0;
}
.bar{
    box-shadow: 1px solid #b98e7a;
    width: 20%;
    margin: 1.5rem auto;
    display: block;

}

.btc{
   font-size: 2rem !important;
   font-weight: 400;
   padding: 1rem 0;
   color: #b98e7a !important;

}

.skills h1{
    font-size: 4rem;
    font-weight: 600;
    padding: 1rem 0;
    color: #b98e7a !important;
    text-align: center;
}
.card  p{
    color: #b98e7a !important;
    font-size: 1.5rem;
}
@media screen and (max-width: 740px) {
    .card-container{
      max-width: 90%;
      margin: auto;
      grid-template-columns: 1fr;
    }
      }

.social{
    margin-top: 1rem ;
    position:absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 640px) {

.social{
        margin-top: 1rem ;
        position:absolute;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

body{
    background-color: #655D8A;
}
