body{
    background-color: #655D8A;
}
.hero-img{
    width:100%;
    background: rgba(0,0,0,0.5);
    height:60vh;
    position: relative;
}
.hero-img::before{
    content: "";
    background:url("https://images.unsplash.com/photo-1509718443690-d8e2fb3474b7?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTF8fHB1cnBsZSUyMHByb2dyYW1taW5nfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=700&q=60");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

}

.hero-img h1{
    font-size: 4rem;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.hero-img p{
    font-size: 2rem;
    text-align: center;
}
@media screen and (max-width: 640px) {

    .hero-img h1{
        font-size: 2rem;
    }


    }
    



.heading{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}